.product-header {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 16px;
}
.product-header-buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}
.product-header-buttons-back {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  border: none;
  background-color: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.product-header-buttons-share {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-color: #f5f5f5;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.4s ease-in-out;
  cursor: pointer;
}
.product-header-buttons-back-icon {
  width: 25px;
  height: 25px;
}
.product-header-buttons-share-icon {
  width: 25px;
  height: 25px;
}
.product-main {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 16px;
  flex-direction: column;
  padding: 0 24px;
}
.product-main-title {
  align-self: flex-start;
  font-size: 24px;
  font-weight: bold;
}
.product-main-price {
  align-self: flex-start;
  font-size: 22px;
  font-weight: bold;
}
.product-main-like-icon-container {
  width: 50px;
  height: 50px;
  border: 3px solid #f5f5f5;
  border-radius: 50%;
  padding: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
  cursor: pointer;
}

.product-main-info-container {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  margin-top: 32px;
  flex-direction: column;
}
.product-main-hr {
  height: 3px;
  width: 100%;
  background-color: #f5f5f5;
  margin-top: 16px;
  border-radius: 24px;
}
.product-main-size-container {
  display: flex;
  width: 100%;
  margin-top: 16px;
  align-items: center;
  justify-content: flex-start;
}
.product-main-size-title {
  font-size: 18px;
  font-weight: bold;
  margin-right: 8px;
}
.product-main-sizes {
  margin-left: 8px;
  width: 100%;
  white-space: nowrap;
  overflow: auto;
  scrollbar-width: none;
}
.product-main-size {
  border: 2px solid #e8ebee;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  align-items: center;
  justify-content: center;
  margin-right: 16px;
  font-weight: bold;
  color: #f5f5f5;
  display: inline-block;
  text-align: center;
  padding-top: 12px;
}

.product-footer-container {
  width: 100%;
  padding: 24px 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.product-footer-buttons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.product-footer-buttons-buy {
  height: 70px;
  background-color: #121212;
  border-radius: 40px;
  width: 250px;
  margin-right: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffff;
  font-size: 24px;
  text-decoration: none;
}
.product-footer-buttons-myCart {
  height: 70px;
  width: 70px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #121212;
  padding: 20px;
  background-color: transparent;
}
.product-footer-buttons-myCart:active {
  opacity: 30%;
}

.slider {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 16px;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 500px;
}

.image {
  width: 300px;
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  border-radius: 24px;
}

.right-arrow {
  width: 30px;
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  opacity: 50%;
  user-select: none;
  transform: translateY(-50%);
}

.left-arrow {
  width: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 32px;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  opacity: 50%;
  user-select: none;
}
.slide {
  opacity: 0;
  transition: 1s ease;
}
.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}

@media (min-width: 576px) {
}

@media (min-width: 992px) {
  .product-footer-buttons-buy {
    margin-right: 16px;
    width: 300px;
  }
  .image {
    width: 500px;
    height: 700px;
  }
  .product-header-buttons {
    padding: 0 16px;
  }
  .product-main-info-container {
    padding: 0 32px;
    margin-top: 48px;
  }
  .slider {
    height: 700px;
  }
  .product-main-size-container {
    padding: 0 0;
  }
  .product-footer-buttons {
    justify-content: flex-start;
    padding: 0 24px;
    margin-top: 16px;
  }
}
@media (min-width: 1200px) {
  .left-arrow {
    left: 0;
  }
  .right-arrow {
    right: 0;
  }
  .product-container {
    padding: 0 32px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .product-main {
    flex-direction: row;
    justify-content: space-between;
    height: 700px;
    align-items: flex-start;
    margin-top: 100px;
  }
  .product-main-info-container {
    justify-content: space-between;
    height: 700px;
    margin-left: 16px;
  }
}
@media (min-width: 1400px) {
}
